:host code[class*="language-"],
:host pre[class*="language-"] {
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  color: #6A6E73;
  background: #F5F5F5;
  font-family: pfe-var(font-family--code);
  font-size: 1em;
  line-height: 1.5em;
  tab-size: 4;
  hyphens: none;
}

:host code[class*="language-"]::selection,
:host pre[class*="language-"]::selection,
:host code[class*="language-"] ::selection,
:host pre[class*="language-"] ::selection {
  background: #cceae7;
  color: #263238;
}

:host :not(pre) > code[class*="language-"] {
  white-space: normal;
  border-radius: 0.2em;
  padding: 0.1em;
}

:host pre[class*="language-"] {
  overflow: auto;
  position: relative;
  margin: 0.5em 0;
  padding: 1.25em 1em;
}

:host .language-css > code,
:host .language-sass > code,
:host .language-scss > code {
  color: #B300B3;
}

:host [class*="language-"] .namespace {
  opacity: 0.7;
}

